import { ICityItem, Gender, YesNo } from 'friendshome-api'

export const getLabelSterilization = (gender: Gender | undefined) => {
  switch (gender) {
    case Gender.male:
      return ''
    case Gender.female:
      return ''
  }
  return ' / '
}

export const getLabelYesNo = (value: YesNo) => {
  switch (value) {
    case YesNo.yes:
      return ''
    case YesNo.no:
      return ''
  }
  return ''
}

export const getLabelAge = (age: number) => {
  age = age % 100

  if (age >= 11 && age <= 19) {
    return ''
  } else {
    const i = age % 10
    switch (i) {
      case (1):
        return  ''
      case (3):
        return  ''
      case (4):
        return ''
    }
  }

  return ''
}

export const getLabelCity = (city: ICityItem | undefined) => {
  return city ? city.name : '  '
}
